
import { verNews } from '../../../service/getData'
import Panel from 'vux/src/components/panel/index.vue'
import Group from 'vux/src/components/group/index.vue'
import Radio from 'vux/src/components/radio/index.vue'
import LoadMore from 'vux/src/components/load-more/index.vue'

import loading from '../../../components/loading'
export default{
data () {
return {
newsdata: [],
showloadmore: false,
showloading: true,
currentPage: 1,
lastPage: 1,
tip:"正在加载",
type: '5',
bottom: 0,
list: []
}
},
components: {
Panel,
LoadMore,
Group,
Radio,
loading
},
methods: {
loadMore(){
let _this = this;
clearTimeout(this.timer);
this.timer=setTimeout(()=>{
var clientHeight=document.documentElement.clientHeight; 


if(document.body.scrollTop){
var scrollTop=document.body.scrollTop;
}
else{
var scrollTop=document.documentElement.scrollTop;
}
var scrollHeight=document.documentElement.scrollHeight;
if(clientHeight+scrollTop+20>=scrollHeight){
this.currentPage++;
new verNews(this.currentPage).then(res=>{
if (res.data.data.length == 0) {
_this.tip="暂无数据";
}else{
for (var i = res.data.data.length - 1; i >= 0; i--) {
_this.list.push(res.data.data[i]);
}
}
})
}
},100);
},
},
watch: {
},
created () {
},
beforeDestroy(){
window.removeEventListener('scroll',this.loadMore,true);
},
async mounted () {
window.addEventListener('scroll',this.loadMore,true);
const newdata = await verNews(this.currentPage)
this.list = newdata.data.data
this.showloading = false
this.showloadmore = true
}
}



