
import Tabbar from 'vux/src/components/tabbar/tabbar.vue'
import TabbarItem from 'vux/src/components/tabbar/tabbar-item.vue'

import { posts } from '../../service/getData'
import headMenu from '../../components/headMenu'
import bbsItem from '../../components/bbsItem'
import icon from '../common/icon'
import loading from '../../components/loading'

export default {
components: {
headMenu,
bbsItem,
icon,
Tabbar,
TabbarItem,
loading
},
data () {
return {
bbsList: []

}
},
methods: {
gochat: function(){
this.$router.push({'path':'/checkuser'});
}
},
computed: {
},
activated: function() {
new posts().then(res => {
this.bbsList = res.data.data;
})
},
async mounted () {
var res = await posts();
this.bbsList = res.data.data;
}
}



