
import Tabbar from 'vux/src/components/tabbar/tabbar.vue'
import TabbarItem from 'vux/src/components/tabbar/tabbar-item.vue'

import { postdetails } from '../../service/getData'
import { commentlist } from '../../service/getData'
import headMenu from '../../components/headMenu'
import comment from '../../components/comment'
import icon from '../common/icon'
import loading from '../../components/loading'

export default {
components: {
headMenu,
comment,
icon,
Tabbar,
TabbarItem,
loading
},
data () {
return {
post:{},
post_id: '',
comments: []

}
},
methods: {
},
computed: {
},
async mounted () {
this.post_id = this.$route.params.id;
var res = await postdetails(this.post_id);
this.post = res.data.data;
var res = await commentlist(this.post_id);
this.comments = res.data.data;
}
}



