
import XHeader from 'vux/src/components/x-header/index.vue'
import Tabbar from 'vux/src/components/tabbar/tabbar.vue'
import TabbarItem from 'vux/src/components/tabbar/tabbar-item.vue'

import { gameshow } from '../../service/getData'
import matchList from '../../components/matchlist'
import headMenu from '../../components/headMenu'
import loading from '../../components/loading'
import icon from '../common/icon'
export default {
data () {
return {
gamesdata: [],
showloading: true
}
},
components: {
XHeader,
matchList,
Tabbar,
TabbarItem,
icon,
loading,
headMenu
},
async mounted () {
const games = await gameshow()
this.gamesdata = games.data
this.showloading = false
}
}



