import TabbarItem from 'vux/src/components/tabbar/tabbar-item.vue'
import XHeader from 'vux/src/components/x-header/index.vue'


import dataList from '../../components/datalist'
const list = () => ['战士', '法师', '刺客', '坦克', '射手', '辅助']
export default {
components: {
Tab,
TabItem,
Tabbar,
TabbarItem,
XHeader,
dataList
},
data () {
return {
list2: list(),
newselected: '战士',
index: 0
}
},
methods: {
},
computed: {
selectedurl: function () {
return '/api/heros/types/' + this.index
}
},
async mounted () {
}
}



