
import XHeader from 'vux/src/components/x-header/index.vue'
import Group from 'vux/src/components/group/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import Blur from 'vux/src/components/blur/index.vue'
import Cell from 'vux/src/components/cell/index.vue'

import { recordlist } from '../../service/getData'
import gameList from '../../components/gamelist'
export default{
data () {
return {
areaid: '',
userid: '',
reload: false,
gamedata: []
}
},
components: {
XHeader,
Group,
XButton,
Blur,
Cell,
gameList
},
mounted () {
this.areaid = this.$route.params.areaid
this.userid = this.$route.params.userid
recordlist(this.areaid, this.userid).then(res => {
if (!res.data) {
alert('服务器繁忙，稍后再试')
} else if (res.data.status === 0) {
this.gamedata = res.data.data
this.reload = false
} else {
this.reload = true
}
})
},
methods: {
load: function (argument) {
window.location.reload()
}
}
}



