
import Panel from 'vux/src/components/panel/index.vue'
import Group from 'vux/src/components/group/index.vue'
import Radio from 'vux/src/components/radio/index.vue'
import Grid from 'vux/src/components/grid/grid.vue'
import GridItem from 'vux/src/components/grid/grid-item.vue'
import GroupTitle from 'vux/src/components/group-title/index.vue'

export default{
data () {
return {
herolist: []
}
},
components: {
Panel,
Group,
Radio,
Grid,
GridItem,
GroupTitle
},
methods: {
getSrc: function(item){
var src_name = this.dtype + '_img';
var src = eval("item." + src_name);
return src;
},
getName: function(item){
var src_name = this.dtype + '_name';
var src = eval("item." + src_name);
return src;
}
},
computed: {
},
props: ['selectedurl', 'tourl', 'dtype'],
async mounted () {
this.$axios.get(this.selectedurl).then((response) => {
this.herolist = response.data.data
})
.catch(function (error) {
})
}
}



